<template>
  <div class="content">
    <div class="content__left">
      <p class="content__left-deg">
        {{ Math.round(weatherInfo.current.temp) }}°
      </p>
      <p class="content__left-day">Today</p>
      <p class="content__left-time">Time: {{ getTime }}</p>
      <p class="content__left-city">City: {{ weatherInfo.name }}</p>
      <div class="content__left-img" v-if="description == 'clear sky'">
        <img src="../assets/images/animated/day.svg" alt="" />
      </div>
      <div class="content__left-img" v-else-if="description == 'light rain'">
        <img src="../assets/images/animated/rainy-1.svg" alt="" />
      </div>
      <div class="content__left-img" v-else-if="description == 'moderate rain'">
        <img src="../assets/images/animated/rainy-2.svg" alt="" />
      </div>
      <div class="content__left-img" v-else-if="description == 'heavy intensity rain'">
        <img src="../assets/images/animated/rainy-3.svg" alt="" />
      </div>
      <div class="content__left-img" v-else-if="description == 'light snow'">
        <img src="../assets/images/animated/snowy-1.svg" alt="" />
      </div>
      <div class="content__left-img" v-else-if="description == 'Snow'">
        <img src="../assets/images/animated/snowy-5.svg" alt="" />
      </div>
      <div class="content__left-img" v-else-if="description == 'Heavy snow'">
        <img src="../assets/images/animated/snowy-6.svg" alt="" />
      </div>
      <div class="content__left-img" v-else-if="description == 'thunderstorm'">
        <img src="../assets/images/animated/thunder.svg" alt="" />
      </div>
      <div class="content__left-img" v-else>
        <img src="../assets/images/animated/cloudy.svg" alt="" />
      </div>
    </div>
    <div class="content__right">
      <img src="../assets/images/cloud.png" alt="" class="content__right-cloud" />
      <div class="content__info">
        <div class="content__info-img">
          <img src="../assets/images/temp.svg" alt="" />
        </div>
        <h3 class="content__info-title">Температура</h3>
        <p class="content__info-text">
          {{ Math.round(weatherInfo.current.temp) }}°
          feels like
          {{ Math.round(weatherInfo.current.feels_like) }}°</p>
      </div>
      <div class="content__info">
        <div class="content__info-img">
          <img src="../assets/images/davleniya.svg" alt="" />
        </div>
        <h3 class="content__info-title">Pressure</h3>
        <p class="content__info-text">{{ weatherInfo.current.pressure }} mm mercury column</p>
      </div>
      <div class="content__info">
        <div class="content__info-img">
          <img src="../assets/images/yomgir.svg" alt="" />
        </div>
        <h3 class="content__info-title">humidity</h3>
        <p class="content__info-text">{{ weatherInfo.current.humidity }}%</p>
      </div>
      <div class="content__info">
        <div class="content__info-img">
          <img src="../assets/images/wind.svg" alt="" />
        </div>
        <h3 class="content__info-title">Wind</h3>
        <p class="content__info-text">{{ weatherInfo.current.wind_speed }} m/s southwest</p>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  computed: {
    ...mapState(["weatherInfo"]),
    getTime() {
      return new Date().toLocaleString("en-US", { timeStyle: "short",  hourCycle: "h23", timeZone: this.weatherInfo.timezone });
    },
    description() {
      return this.weatherInfo.current.weather[0].description;
    },
  },
};
</script>
<style lang="">
</style>