<template >
  <div class="main__day">
    <h3 class="main__day-title">{{ getWeek }}</h3>
    <p class="main__day-date">{{ getDay }} {{ getMonth }}</p>
    <div class="main__day-img">
      <div v-if="description == 'clear sky'">
        <img src="../assets/images/animated/day.svg" alt="" />
      </div>
      <div v-else-if="description == 'light rain'">
        <img src="../assets/images/animated/rainy-1.svg" alt="" />
      </div>
      <div v-else-if="description == 'moderate rain'">
        <img src="../assets/images/animated/rainy-2.svg" alt="" />
      </div>
      <div v-else-if="description == 'heavy intensity rain'">
        <img src="../assets/images/animated/rainy-3.svg" alt="" />
      </div>
      <div v-else-if="description == 'light snow'">
        <img src="../assets/images/animated/snowy-1.svg" alt="" />
      </div>
      <div v-else-if="description == 'Snow'">
        <img src="../assets/images/animated/snowy-5.svg" alt="" />
      </div>
      <div v-else-if="description == 'Heavy snow'">
        <img src="../assets/images/animated/snowy-6.svg" alt="" />
      </div>
      <div v-else-if="description == 'thunderstorm'">
        <img src="../assets/images/animated/thunder.svg" alt="" />
      </div>
      <div v-else>
        <img src="../assets/images/animated/cloudy.svg" alt="" />
      </div>
    </div>

    <p class="main__day-max">
      {{
        Math.round(day.temp.max) >= 0
          ? "+" + Math.round(day.temp.max)
          : Math.round(day.temp.max)
      }}°
    </p>
    <p class="main__day-min">
      {{
        Math.round(day.temp.min) >= 0
          ? "+" + Math.round(day.temp.min)
          : Math.round(day.temp.min)
      }}°
    </p>
    <p class="main__day-descr">{{ description }}</p>
  </div>
</template>
<script>
export default {
  props: {
    day: Object,
  },
  computed: {
    description() {
      return this.day.weather[0].description;
    },
    getWeek() {
      let milliSec = this.day.dt * 1000;
      const date = new Date(milliSec);
      return date.toLocaleString("en-US", {
        weekday: "long",
      });
    },
    getMonth() {
      let milliSec = this.day.dt * 1000;
      const date = new Date(milliSec);
      return date.toLocaleString("en-US", {
        month: "short",
      });
    },
    getDay() {
      let milliSec = this.day.dt * 1000;
      const date = new Date(milliSec);
      return date.toLocaleString("en-US", {
        day: "numeric",
      });
    },
  },
};
</script>
