import { createStore } from 'vuex'
import axios from 'axios';
const store = createStore({
    state: {
        apiKey: "818427e437c3be231b76ef0d26bceb0e",
        weatherInfo: null
    },
    mutations: {
        setWeather(state, payload) {
            state.weatherInfo = payload
        }
    },
    actions: {
        async getCountry({ commit, state }, city) {
            try {
                let res = await axios.get(`https://api.openweathermap.org/geo/1.0/direct?q=${city}&appid=${state.apiKey}`)
                let cordinate = res.data[0]
                const { lat, lon, name } = cordinate
                let res2 = await axios.get(`https://api.openweathermap.org/data/2.8/onecall?lat=${lat}&lon=${lon}&exclude=minutely,hourly,alerts&appid=${state.apiKey}&units=metric`)
                let data = res2.data
                let weather = { ...data, name }
                commit("setWeather", weather)
            } catch (error) {
                console.log(error);
            }
        }
    },
    getters: {
        getWeatherInfo(state) {
            return state.weatherInfo.daily.filter((day, i) => i < state.weatherInfo.daily.length - 1)
        }
    }
})
export default store