<template >
  <nav class="nav">
    <a href="#!" class="nav__logo">
      <img src="../assets/images/animated/cloudy-day-1.svg" alt="" />
      weather
    </a>
    <input
      class="nav__input"
      type="text"
      placeholder="Choose City"
      v-model="search"
      @keydown.enter="getDate()"
    />
    <p style="display:none">
      {{ getTime }}
    </p>
  </nav>
</template>
<script>
let body = document.querySelector('body');
import { mapActions, mapState } from 'vuex'
export default {
  data() {
    return {
      search: "",
    };
  },
  computed: {
    ...mapState(["sunOrNight", "weatherInfo"]),
    getTime() {
      return new Date().toLocaleString("en-US", {
        timeZone: this.weatherInfo.timezone,
        timeStyle: "short",
        hourCycle: "h23",
      });
    },
  },
  methods:{
    ...mapActions(["getCountry"]),
    getDate(){
      this.getCountry(this.search)
      this.search = ""
    },
    newSearch(val) {
      console.log();
      if (Number(val[0] + val[1]) >= 18 && Number(val[0] + val[1]) < 24 ||Number(val[0]) == 0 && Number(val[1]) >= 0 && Number(val[1]) <= 6) {
        body.classList.remove("sun")
        body.classList.add("night")
      } else {
        body.classList.remove("night")
        body.classList.add("sun")
      }
    }
  },
  mounted() {
    this.newSearch(this.getTime)
  },
  updated() {
    this.newSearch(this.getTime)
  }
};
</script>
